.side-menu {
  min-height: 100vh!important;
}
  .side-menu .logo {
    color: #fff;
    position: relative;
    text-align: center;
    height: 3rem;
    line-height: 3rem;
    cursor: pointer;
    overflow: hidden;
  }
  .side-menu .logo img {
      width: 2.5rem;
      height: 2.5rem;
    }
  .side-menu .logo .title {
      font-size: 1.5rem;
      display: inline-block;
    }
  .side-menu .r-content .ant-layout-header {
      height: 52px;
      line-height: 52px;
      background: #ffffff;
      padding: 0 .1rem!important;
    }
  .side-menu .r-content .ant-layout-header .trigger {
        font-size: 1.5rem;
        padding: 0 1rem;
      }
  .side-menu .r-content .ant-layout-header .timer {
        font-weight: bold;
        font-size: 1.5rem;
        float: right;
        margin-right: 2rem;
      }


